.overlayModal {
  display: flex;
  display: -webkit-flex;
  flex-direction: column;
  -webkit-box-orient: vertical;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;

  position: fixed;
  top: 0;
  width: 528px;
  min-width: 320px;
  min-height: 100vh;
  background-color: rgba(0,0,0,0.65);
  z-index: 100;
  user-select: none;
}
  
  
  .overlayModal .header {
    color: white;
    font-family: "SpoqaRegular";
    font-size: 1.4em;
    z-index: 150;
  }
  
  .overlayModal .modal {
    width: 90%;
    z-index: 150;
    margin-top: 1.1055em;
  
    background-color: white;
    border-radius: 0.737em;
  }
  
  
.overlayModal .modal .buttonSection {
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    -webkit-justify-content: space-between;
    padding: 1.053em 0.526em;
    margin: 0 1.053em;
    font-size: 1.4em;
    font-family: "SpoqaMedium";
    border-top: 1px solid rgba(0,0,0,0.06);
  }
  
  .overlayModal .modal .buttonSection .close{
    cursor: pointer;
    color: #595959;
  }
  .overlayModal .modal .buttonSection .apply{
    cursor: pointer;
    color: #3FB8D5;
  }
  .overlayModal .modal .buttonSection .apply.unactive{
    color: #A3A3A3;
  }
  
  
  
  @media (max-width: 528px){
    .overlayModal {
        width: 100%;
    }
  }
  @media (orientation: portrait) and (max-width: 768px){
    .overlayModal {
        width: 100%;
    }
  }